import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class CetakLaporanServiceExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let total_barang = 0;
    let total_berat = 0;
    let total_ongkos = 0;
    let total_jumlah = 0;

    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        item.detail_barang.forEach((detail_barang, index) => {
          total_ongkos = total_ongkos +  parseInt(detail_barang.ongkos) * parseInt(detail_barang.jumlah);
          total_jumlah = total_jumlah + detail_barang.jumlah;
          total_berat = total_berat + detail_barang.berat;
        })
        total_barang = total_barang + item.detail_barang.length;
      });

    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Transaksi Service"
          sheet="Laporan Transaksi Service"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="7" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN TRANSAKSI SERVICE{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="7"> Periode </th>
            </tr>
            <tr>
              <th colSpan="7">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    "s/d" +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="7"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="7"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="7"></th>
            </tr>
            <tr>
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                TANGGAL TRANSAKSI
              </th>
              {/* <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                TANGGAL SELESAI
              </th> */}
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                KODE SALES
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                NAMA CUSTOMER
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                NO HP
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                ALAMAT CUSTOMER
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                KODE GUDANG
              </th>
              <th
                style={{
                  backgroundColor: "#E8E5E5", color: "#000"
                }}
              >
                TOTAL SERVICE
              </th>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((item, index) => (
              <div key={index}>
                {/* {item._id.pembayaran.map((row, key) => ( */}
                  <>
                    <tr key={index}>
                      <th colSpan="7" style={{ textAlign: "left",border: "1px solid #000" }}>
                      No Service
                        {" "}
                        {item._id.no_faktur_service}{" "}
                      </th>
                    </tr>
                    {/* <tr>
                      <th colSpan="7" style={{ textAlign: "left",border: "1px solid #000" }}>
                        {" "}
                        {row.jenis} : {row.jumlah_rp}{" "}
                      </th>
                    </tr> */}
                  </>

                {item.detail.map((row, key) => (
                  <>
                    <tr  key={key}>
                      <td>
                        {" "}
                        {row.tgl_system}
                      </td>
                      {/* <td>
                        {" "}
                        {row.tgl_selesai}
                      </td> */}
                      <td>
                        {" "}
                        {row.kode_sales}
                      </td>
                      <td>
                        {" "}
                        {row.nama_customer}
                      </td>
                      <td> {row.no_hp}</td>
                      <td>
                        {" "}
                        {row.alamat_customer}
                      </td>
                      <td
                        style={{
                          textAlign: "center",
                        }}
                      >
                        {" "}
                        {row.kode_gudang}
                      </td>
                      <td
                        style={{
                          textAlign: "center",
                        }}
                      >
                        {" "}
                        {row.total}
                      </td>
                    </tr>
                    <tr>
                      <th colSpan="7">
                        {" "}
                        Detail Barang{" "}
                      </th>
                    </tr>
                    <tr>
                      <th
                        colSpan="3"
                        style={{
                          backgroundColor: "#E8E5E5", color: "#000"
                        }}
                      >
                        {" "}
                        NAMA BARANG{" "}
                      </th>
                      <th
                        style={{
                          backgroundColor: "#E8E5E5", color: "#000"
                        }}
                      >
                        {" "}
                        BERAT{" "}
                      </th>
                      <th
                        style={{
                          backgroundColor: "#E8E5E5", color: "#000"
                        }}
                      >
                        {" "}
                        JUMLAH{" "}
                      </th>
                      <th
                        colSpan="2"
                        style={{
                          backgroundColor: "#E8E5E5", color: "#000"
                        }}
                      >
                        {" "}
                        DESKRIPSI{" "}
                      </th>
                    </tr>
                    <>
                      {row.detail_barang.map((detail, index) => (
                        <>
                          <tr>
                            <td
                              colSpan="3"
                              style={{
                                textAlign: "center",
                              }}
                            >
                              {" "}
                              {detail.nama_barang}
                            </td>
                            <td
                              style={{
                                textAlign: "center",
                              }}
                            >
                              {" "}
                              {parseFloat(detail.berat.toFixed(3))}
                            </td>
                            <td
                              style={{
                                textAlign: "center",
                              }}
                            >
                              {" "}
                              {detail.jumlah}
                            </td>
                            <td
                              colSpan="2"
                              style={{
                                textAlign: "center",
                              }}
                            >
                              {" "}
                              {detail.deskripsi}
                            </td>
                          </tr>
                        </>
                      ))}
                    </>
                  </>
                ))}
              </div>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <th
                colSpan="7"
                style={{ border: "1px solid #000", textAlign: "left" }}
              >
                Total Berat : { total_berat.toFixed(3) }<br />
                Total Jumlah : { total_jumlah }<br />
                Total Ongkos : {total_ongkos.toLocaleString("kr-KO")} <br />
                Total Barang : {total_barang }<br />
              </th>
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default CetakLaporanServiceExel;
